.content {
  position: relative;
  height: 100vw;
  margin-top: 2vh;
  background-color: white; }
  .content__background, .content__background__shadow, .content__background__image, .content__area {
    position: absolute;
    top: 0;
    bottom: 0; }
  .content__background__shadow {
    left: 0;
    width: 0%;
    z-index: 2; }
    .content__background__shadow:before {
      content: '';
      position: absolute;
      z-index: 10;
      top: 0;
      bottom: 0;
      left: 100%;
      width: 275px; }
  .content__background__image {
    right: 0;
    width: 70%;
    height: 70%;
    background-position: center 10%;
    background-size: cover;
    z-index: 1; }
  .content__area {
    left: 0;
    right: 0;
    height: 100%;
    z-index: 3; }
    .content__area__container {
      padding: 10px 10px;
      color: #D3D3D3; }
  .content__title {
    font-size: 45px;
    color: white;
    font-weight: 700; }
  .content__description {
    font-size: 18px;
    color: #999;
    margin-top: 20px;
    max-width: 500px; }
  .content__close {
    color: white;
    width: 40px;
    height: 40px;
    background: black;
    outline: none;
    border: none;
    position: absolute;
    top: 30px;
    right: 30px; }



