.slide-button {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 2vw;
  background: transparent;
  border: 0;
  outline: 0;
  padding: 0;
  margin: 5vw 0;
  z-index: 200; }
  .slide-button span {
    width: 25px;
    color: black;
    display: block;
    margin: 0 auto; }
  .slide-button--next {
    right: 0; }
    .slide-button--next span {
      transform: rotateZ(-90deg); }
  .slide-button--prev {
    left: 0; }
    .slide-button--prev span {
      transform: rotateZ(90deg); }

.slider-wrapper {
  overflow: hidden;
  position: relative; }

body {
  width: 100%; }

.slider {
  display: flex;
  position: relative;
  z-index: 100;
  background-color: transparent;
  display: flex;
  width: 100%; }
  .slider__container {
    display: flex;
    transition: transform 400ms ease 100ms;
    z-index: 3;
    width: 100%;
    border: black; }
  .slider:not(.slider--open) .item:hover .show-details-button {
    opacity: 1; }
  .slider:not(.slider--open) .item:hover {
    transform: scale(1.15) !important;
    opacity: 0.85; }

@-webkit-keyframes float {
  0% {
    box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
    transform: translateY(0px); }
  50% {
    box-shadow: 0 25px 15px 0px rgba(0, 0, 0, 0.2);
    transform: translateY(-20px); }
  100% {
    box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
    transform: translateY(0px); } }

@keyframes float {
  0% {
    box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
    transform: translateY(0px); }
  50% {
    box-shadow: 0 25px 15px 0px rgba(0, 0, 0, 0.2);
    transform: translateY(-20px); }
  100% {
    box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
    transform: translateY(0px); } }
  .slider:not(.slider--open):hover .item {
    transform: translateX(0%); }
  .slider:not(.slider--open) .item:hover ~ .item {
    transform: translateX(3.75%); }
  .slider:not(.slider--open) .item:click {
    transform: scale(1.25) !important;
    box-shadow: 15px 5px 10px 0 rgba(0, 0, 0, 0.2), 0 8px 15px 0 rgba(0, 0, 0, 0.19);
    opacity: 0.5; }
  .slider:not(.slider--open):click .item {
    transform: translateX(0%); }
  .slider:not(.slider--open) .item:click ~ .item {
    transform: translateX(3.75%); }

.show-details-button {
  position: absolute;
  z-index: 110;
  bottom: 0;
  left: 0;
  right: 0;
  transition: opacity 300ms ease 100ms;
  background: transparent;
  border: 2px;
  outline: none;
  width: 100%; }
  .show-details-button span {
    display: block;
    width: 14px;
    margin: 0 auto;
    color: white;
    background: transparent; }

.mark {
  box-sizing: border-box;
  border: solid 4px #fff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: -4px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
  z-index: 4; }
  .mark:before, .mark:after {
    position: absolute;
    width: 0;
    height: 0;
    content: '';
    top: 100%;
    left: 50%;
    margin-left: -13px;
    border-style: solid;
    border-width: 7px 13px 0 13px; }
  .mark:before {
    border-color: rgba(0, 0, 0, 0.15) transparent transparent transparent;
    margin-top: 5px; }
  .mark:after {
    margin-top: 4px;
    border-color: #fff transparent transparent transparent; }

